import type { IconDefinition } from "@fortawesome/free-regular-svg-icons";

export type MenuItemType = {
  name: string;
  href: string;
  icon: IconDefinition;
  isRunning: boolean;
};

export function classNames(...classes: any[]) {
  return classes.filter(Boolean).join(" ");
}

export enum MACHINE_STATE {
  ERROR = "error",
  EXITED = "exited",
  LOADING = "loading",
  RUNNING = "running",
}
