import type {
  LinksFunction,
  LoaderFunction,
  MetaFunction,
} from "@remix-run/node";
import { json } from "@remix-run/node";
import {
  Link,
  Links,
  LiveReload,
  Meta,
  NavLink,
  Outlet,
  Scripts,
  ScrollRestoration,
  useLoaderData,
  useLocation,
} from "@remix-run/react";

import tailwindStylesheetUrl from "./styles/tailwind.css";
import { faFolder, faFolderOpen } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Fragment, useState } from "react";
import { Dialog, Transition } from "@headlessui/react";
import type { MenuItemType } from "./utils";
import { classNames, MACHINE_STATE } from "./utils";
import { getChapters } from "./models/chapter.server";
import MachineStatusIcon from "./components/MachineStatusIcon";
import { dockerCommand, Options } from "docker-cli-js";
import {
  faBiohazard,
  faRadiation,
  faSkullCrossbones,
  faSpider,
  faUserSecret,
  faUserNinja,
  faBars,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";
import CommandPalette from "./components/CommandPalette";

export const links: LinksFunction = () => {
  return [{ rel: "stylesheet", href: tailwindStylesheetUrl }];
};

export const meta: MetaFunction = () => ({
  charset: "utf-8",
  title: "Hands-on-hacking",
  viewport: "width=device-width,initial-scale=1",
});

type LoaderData = {
  chapters: Awaited<MenuItemType[]>;
  logoIndex: Awaited<number>;
};

const options = new Options(undefined, undefined, false);

const logos = [
  faUserSecret,
  faSkullCrossbones,
  faUserNinja,
  faRadiation,
  faBiohazard,
  faSpider,
];

export const loader: LoaderFunction = async () => {
  const logoIndex = Math.floor(Math.random() * logos.length);
  const chaptersData = await getChapters();
  const chapters = await Promise.allSettled(
    chaptersData.map(
      (chapter: any) =>
        new Promise((resolve) => {
          dockerCommand(`inspect ${chapter.containerName}`, options)
            .then((data) => {
              const { object } = data;
              const container = object.find(
                (container: any) =>
                  container.Name === `/${chapter.containerName}`
              );
              let isRunning = true;
              if (container.State.Status === MACHINE_STATE.EXITED) {
                isRunning = false;
              }
              resolve({
                name: chapter.menuTitle,
                href: `chapters/${chapter.index}`,
                icon: faFolder,
                isRunning,
              });
            })
            .catch((error) => {
              resolve({
                name: chapter.menuTitle,
                href: `chapters/${chapter.index}`,
                icon: faFolder,
                isRunning: false,
              });
            });
        })
    )
  ).then((data) => {
    return data.map((item: any) => {
      return item.value;
    });
  });
  return json<LoaderData>({
    chapters,
    logoIndex,
  });
};

export default function App() {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const data = useLoaderData() as LoaderData;
  // detect if is in home or in a chapter
  const location = useLocation();

  const isHome = location.pathname === "/";

  const menu = data.chapters.map((item: MenuItemType) => (
    <NavLink
      to={{
        pathname: item.href,
      }}
      key={item.name}
      className={({ isActive }) =>
        classNames(
          isActive
            ? "bg-green-800 text-white"
            : "text-gray-300 hover:text-white",
          "group flex items-center rounded-md px-2 py-2 text-sm font-medium hover:bg-green-700"
        )
      }
      onClick={() => setSidebarOpen(false)}
    >
      <FontAwesomeIcon
        icon={item.isRunning ? faFolderOpen : faFolder}
        className={classNames(
          item.isRunning
            ? "text-green-300"
            : "text-green-400 group-hover:text-green-300",
          "mr-3 h-6 w-6 flex-shrink-0"
        )}
        aria-hidden="true"
      />
      <span className="flex-1">{item.name}</span>
      <MachineStatusIcon isRunning={item.isRunning} hideStoppedState />
    </NavLink>
  ));

  return (
    <html lang="en" className="h-full bg-black">
      <head>
        <Meta />
        <Links />
      </head>
      <body className="h-full font-mono">
        <CommandPalette
          chapters={[
            ...data.chapters,
            {
              name: "Advanced Attacks",
              href: "chapters/advanced",
              icon: faFolder,
              isRunning: false,
            },
          ]}
        />
        <div>
          <Transition.Root show={sidebarOpen} as={Fragment}>
            <Dialog
              as="div"
              className="relative z-40 md:hidden"
              onClose={setSidebarOpen}
            >
              <Transition.Child
                as={Fragment}
                enter="transition-opacity ease-linear duration-300"
                enterFrom="opacity-0"
                enterTo="opacity-100"
                leave="transition-opacity ease-linear duration-300"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
              >
                <div className="fixed inset-0 bg-black bg-opacity-75" />
              </Transition.Child>

              <div className="fixed inset-0 z-40 flex">
                <Transition.Child
                  as={Fragment}
                  enter="transition ease-in-out duration-300 transform"
                  enterFrom="-translate-x-full"
                  enterTo="translate-x-0"
                  leave="transition ease-in-out duration-300 transform"
                  leaveFrom="translate-x-0"
                  leaveTo="-translate-x-full"
                >
                  <Dialog.Panel className="relative flex w-full max-w-xs flex-1 flex-col bg-black">
                    <Transition.Child
                      as={Fragment}
                      enter="ease-in-out duration-300"
                      enterFrom="opacity-0"
                      enterTo="opacity-100"
                      leave="ease-in-out duration-300"
                      leaveFrom="opacity-100"
                      leaveTo="opacity-0"
                    >
                      <div className="absolute top-0 right-0 -mr-12 pt-2">
                        <button
                          type="button"
                          className="ml-1 flex h-10 w-10 items-center justify-center rounded-full focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white"
                          onClick={() => setSidebarOpen(false)}
                        >
                          <span className="sr-only">Close sidebar</span>
                          <FontAwesomeIcon
                            icon={faTimes}
                            className="h-5 w-5 text-white"
                            aria-hidden="true"
                          />
                        </button>
                      </div>
                    </Transition.Child>
                    <div className="fieldset h-0 flex-1 overflow-y-auto pt-5 pb-4">
                      <div className="flex flex-shrink-0 items-center px-4">
                        <div className="h-12 w-full text-center font-mono text-xl font-bold text-green-500">
                          <Link to=".">
                            <FontAwesomeIcon
                              className="mx-auto h-full"
                              icon={logos[data.logoIndex]}
                            />
                          </Link>
                        </div>
                      </div>
                      <nav className="mt-5 space-y-2 border-t-4 border-double border-green-600 px-2 pt-6">
                        {menu}
                      </nav>
                    </div>
                  </Dialog.Panel>
                </Transition.Child>
                <div className="w-14 flex-shrink-0">
                  {/* Force sidebar to shrink to fit close icon */}
                </div>
              </div>
            </Dialog>
          </Transition.Root>

          {/* Static sidebar for desktop */}
          <div className="hidden md:fixed md:inset-y-0 md:flex md:w-64 md:flex-col md:pr-3">
            {/* Sidebar component, swap this element with another sidebar if you like */}
            <div className="flex min-h-0 flex-1 flex-col">
              <div className="flex flex-1 flex-col overflow-y-auto">
                <fieldset className="fieldset">
                  <legend className="legend">Logo</legend>
                  <div className="flex h-24 flex-shrink-0 items-center justify-center">
                    <div className="h-12 w-full text-center font-mono text-xl font-bold text-green-500">
                      <Link to=".">
                        <FontAwesomeIcon
                          className="mx-auto h-full"
                          icon={logos[data.logoIndex]}
                        />
                      </Link>
                    </div>
                  </div>
                </fieldset>
                <fieldset className="fieldset mt-3 flex-1 ">
                  <legend className="legend">Menu</legend>
                  <nav className=" space-y-1 px-2 pt-5">{menu}</nav>
                </fieldset>
              </div>
            </div>
          </div>
          <div className="flex flex-1 flex-col md:pl-64">
            <div className="sticky top-0 z-10 bg-black pl-1 pt-1 sm:pl-3 sm:pt-3 md:hidden">
              <button
                type="button"
                className="-ml-0.5 -mt-0.5 inline-flex h-12 w-12 items-center justify-center rounded-md text-gray-500 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-blue-500"
                onClick={() => setSidebarOpen(true)}
              >
                <span className="sr-only">Open sidebar</span>
                <FontAwesomeIcon
                  icon={faBars}
                  aria-hidden="true"
                  className="h-5 w-5 text-white"
                />
              </button>
            </div>
            <main className="w-full flex-1 text-gray-300">
              <fieldset className="fieldset min-h-screen">
                <legend className="legend">
                  {isHome ? "Welcome" : "Vulnerability"}
                </legend>
                <Outlet />
              </fieldset>
            </main>
          </div>
        </div>
        <ScrollRestoration />
        <Scripts />
        <LiveReload />
      </body>
    </html>
  );
}
