import {
  Form,
  useCatch,
  useLoaderData,
  useParams,
  useTransition,
} from "@remix-run/react";
import type { ActionFunction, LoaderFunction } from "@remix-run/server-runtime";
import { json } from "@remix-run/node";
import { dockerCommand, Options } from "docker-cli-js";
import invariant from "tiny-invariant";
import { getChapter } from "~/models/chapter.server";
import { classNames, MACHINE_STATE } from "~/utils";
import { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowUpRightFromSquare,
  faCircleNotch,
} from "@fortawesome/free-solid-svg-icons";
import { faPlay, faStop } from "@fortawesome/free-solid-svg-icons";
import MachineStatusIcon from "~/components/MachineStatusIcon";
import childProcess from "child_process";

type LoaderData = {
  title?: string;
  subTitle?: string;
  description?: string;
  containerState?: any;
  ports?: any;
  hasContainer?: boolean;
};

export function ErrorBoundary({ error }: { error: Error }) {
  console.error(error);
  return (
    <div className="py-6">
      <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
        An unexpected error occurred: {error.message}
      </div>
    </div>
  );
}

export function CatchBoundary() {
  const caught = useCatch();

  if (caught.status === 404) {
    return (
      <div className="py-6">
        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          Chapter not found
        </div>
      </div>
    );
  }

  throw new Error(`Unexpected caught response with status: ${caught.status}`);
}

export const action: ActionFunction = async ({ request }) => {
  const formData = await request.formData();
  const action = formData.get("action");
  const chapterId = formData.get("chapterId");

  const chapter = await getChapter({ index: Number(chapterId) });
  if (!chapter) {
    throw new Response("Not Found", { status: 404 });
  }
  // TODO: validate action to prevent a vulnerability.
  const dockerAction =
    action === "start" ? "up -d" : action === "stop" ? "down" : action;

  try {
    childProcess
      .execSync(
        `docker compose -p ${chapter.composerName} -f ${chapter.composerFileLocation} ${dockerAction}`
      )
      .toString();
  } catch (error: any) {
    throw new Response(error.stderr.toString(), { status: 500 });
  }
  return null;
};

export const loader: LoaderFunction = async ({ params }) => {
  const options = new Options(undefined, undefined, false);
  invariant(params.chapterId, "chapterId not found");
  const chapterId = Number(params.chapterId);
  if (Number.isNaN(chapterId)) {
    throw new Response("Not Found", { status: 404 });
  }
  const chapter = await getChapter({ index: Number(chapterId) });
  if (!chapter) {
    throw new Response("Not Found", { status: 404 });
  }
  let container = {
    State: {
      Status: "exited",
    },
    NetworkSettings: {
      Ports: [],
    },
  };
  try {
    let data = await dockerCommand(`inspect ${chapter.containerName}`, options);
    const { object } = data;
    container = object.find(
      (container: any) => container.Name === `/${chapter.containerName}`
    );
  } catch (e) {
    // Gracefully continue and discard errors.
  }
  return json<LoaderData>({
    containerState: container.State,
    ports: container.NetworkSettings.Ports,
    title: chapter.title,
    subTitle: `Chapter ${chapter.index}`,
    description: chapter.description,
    hasContainer: chapter.containerName !== "",
  });
};

const MachineStatusAlert = ({ state }: { state: MACHINE_STATE }) => {
  let message = "Instance is running";
  let isRunning = true;
  if (![MACHINE_STATE.RUNNING, MACHINE_STATE.EXITED].includes(state)) {
    return null;
  } else if (state === MACHINE_STATE.EXITED) {
    message = "Instance is stopped";
    isRunning = false;
  }
  return (
    <div
      className={classNames(
        isRunning ? "border-green-800" : "border-red-800",
        "border-2 p-4"
      )}
    >
      <div className="flex items-center">
        <div className="flex-0">
          <MachineStatusIcon isRunning={isRunning} />
        </div>
        <div className="flex-1 text-center">
          <h3
            className={classNames(
              isRunning ? "text-green-500" : "text-red-500",
              "text-sm font-medium uppercase"
            )}
          >
            {message}
          </h3>
        </div>
      </div>
    </div>
  );
};
interface DetailsProps {
  state: MACHINE_STATE;
  chapterId?: string;
  isUpdating: boolean;
  ports: any;
  location: string;
}
const Details = ({
  state,
  chapterId,
  isUpdating,
  ports,
  location,
}: DetailsProps) => (
  <>
    <h2 className="sr-only">Details</h2>
    <div className="mt-2">
      <MachineStatusAlert state={state} />
    </div>
    <div className="mt-6 border-t border-green-900 pt-6 pb-6 xl:pb-0">
      <Form method="post" className="flex justify-center">
        <input type="hidden" name="chapterId" value={chapterId} />
        <button
          type="submit"
          name="action"
          value={state === MACHINE_STATE.RUNNING ? "stop" : "start"}
          disabled={isUpdating}
          className={classNames(
            state === MACHINE_STATE.RUNNING
              ? "bg-red-600 hover:bg-red-700 focus:ring-red-500"
              : "bg-green-600 hover:bg-green-700 focus:ring-green-500",
            isUpdating ? "cursor-not-allowed opacity-50" : "",
            "inline-flex items-center rounded-md border border-transparent px-4 py-2 text-sm font-medium uppercase text-white shadow-sm  focus:outline-none focus:ring-2  focus:ring-offset-2"
          )}
        >
          <FontAwesomeIcon
            icon={
              isUpdating
                ? faCircleNotch
                : state === MACHINE_STATE.RUNNING
                ? faStop
                : faPlay
            }
            className={classNames(
              isUpdating ? "animate-spin-slow" : "",
              "-ml-1 mr-2 h-5 w-5"
            )}
            aria-hidden="true"
          />
          {state === MACHINE_STATE.RUNNING
            ? `Stop${isUpdating ? "ping" : ""}`
            : `Start${isUpdating ? "ing" : ""}`}{" "}
          instance
        </button>
      </Form>
    </div>
    {Object.keys(ports).length ? (
      <fieldset className="fieldset-thin mb-6 xl:mt-6 xl:mb-0">
        <legend className="legend">Open ports</legend>
        <ul>
          {Object.keys(ports)
            .map((port) => ports[port][0].HostPort)
            .map((port) => (
              <li key={port} className="my-2">
                <a
                  href={`${location}:${port}`}
                  target="_blank"
                  rel="noreferrer"
                  className="m-3 flex cursor-pointer items-center bg-green-600 p-2 uppercase text-black hover:bg-green-700"
                >
                  <span className="flex-1">
                    {location.substring(2)}:{port}
                  </span>
                  <FontAwesomeIcon
                    icon={faArrowUpRightFromSquare}
                    className="h-4"
                  />
                </a>
              </li>
            ))}
        </ul>
      </fieldset>
    ) : null}
  </>
);

const MissingContainer = () => <div>This chapter doesn't need a container</div>;

export default function ChapterIdIndexPage() {
  const [location, setLocation] = useState("");
  const params = useParams();
  const data = useLoaderData() as LoaderData;
  const transition = useTransition();
  const isUpdating = Boolean(transition.submission);
  const chapterId = params.chapterId;
  const { title, subTitle, description, containerState, ports, hasContainer } =
    data;

  useEffect(() => {
    setLocation(`//${window.location.hostname}`);
  }, []);

  const detailsBlock = (
    <Details
      state={containerState.Status}
      isUpdating={isUpdating}
      ports={ports}
      chapterId={chapterId}
      location={location}
    />
  );

  return (
    <main className="flex-1">
      <div className="py-8 xl:py-10">
        <div className="mx-auto max-w-3xl px-4 sm:px-6 lg:px-8 xl:grid xl:max-w-6xl xl:grid-cols-3">
          <div className="xl:col-span-2 ">
            <fieldset className="fieldset-single p-4">
              <legend className="legend">Info</legend>
              <div className="border-green-900 md:flex md:items-center md:justify-between md:space-x-4 xl:border-b xl:pb-6">
                <div className="w-full space-y-2">
                  <h2 className="border border-orange-400 p-1 text-center text-xs font-bold uppercase text-orange-400">
                    {subTitle}
                  </h2>
                  <h1 className=" bg-yellow-400 p-1 text-center text-2xl font-bold text-black">
                    {title}
                  </h1>
                </div>
              </div>
              <aside className="mt-8 border-b border-green-900 xl:hidden">
                {hasContainer && detailsBlock}
                {!hasContainer && (
                  <div className="pb-8 text-center">
                    <MissingContainer />
                  </div>
                )}
              </aside>
              <div className="py-3 xl:pt-6 xl:pb-0">
                <h2 className="sr-only">Description</h2>
                <div className="prose max-w-none space-y-3">
                  {description?.split("\n").map((line, index) => (
                    <p key={index}>{line}</p>
                  ))}
                </div>
              </div>
            </fieldset>
          </div>
          <aside className="hidden xl:block xl:pl-8">
            <fieldset className="fieldset-single px-4 pb-4">
              <legend className="legend">Details</legend>
              {hasContainer && detailsBlock}
              {!hasContainer && (
                <div className="mt-3 text-center">
                  <MissingContainer />
                </div>
              )}
            </fieldset>
          </aside>
        </div>
      </div>
    </main>
  );
}
