import { classNames } from "~/utils";

export interface MachineStatusIconProps {
  isRunning: boolean;
  hideStoppedState?: boolean;
}

const MachineStatusIcon = ({
  isRunning,
  hideStoppedState = false,
}: MachineStatusIconProps) => {
  const canShowSmallIcon = isRunning || !hideStoppedState;
  return (
    <div className="relative flex h-3 w-3 items-center justify-center">
      <span
        className={classNames(
          isRunning ? "bg-green-200 motion-safe:animate-ping-slow" : "",
          "absolute h-full w-full rounded-full opacity-75"
        )}
      ></span>
      {canShowSmallIcon && (
        <span
          className={classNames(
            isRunning ? "bg-green-500" : "bg-red-500",
            "absolute h-2 w-2 rounded-full"
          )}
        ></span>
      )}
    </div>
  );
};

export default MachineStatusIcon;
