import { Fragment, useEffect, useState } from "react";
import { Combobox, Dialog, Transition } from "@headlessui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import type { MenuItemType } from "~/utils";
import { classNames } from "~/utils";
import { useNavigate } from "@remix-run/react";

export default function CommandPalette({
  chapters,
}: {
  chapters: MenuItemType[];
}) {
  const [query, setQuery] = useState("");
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const onKeydown = (e: KeyboardEvent) => {
      if (e.key === "Escape") {
        setOpen(false);
      } else if (e.key === "k" && (e.metaKey || e.ctrlKey)) {
        setOpen(!open);
      }
    };
    window.addEventListener("keydown", onKeydown);
    return () => {
      window.removeEventListener("keydown", onKeydown);
    };
  }, [open]);

  const filteredChapters =
    query === ""
      ? []
      : chapters.filter((chapter) => {
          return chapter.name.toLowerCase().includes(query.toLowerCase());
        });

  return (
    <Transition.Root
      show={open}
      as={Fragment}
      afterLeave={() => setQuery("")}
      appear
    >
      <Dialog as="div" className="relative z-50" onClose={setOpen}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black bg-opacity-75 transition-opacity" />
        </Transition.Child>

        <div className="fixed inset-0 z-10 overflow-y-auto p-4 sm:p-6 md:p-20">
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95"
          >
            <Dialog.Panel
              as="fieldset"
              className="fieldset mx-auto max-w-xl transform overflow-hidden rounded-xl bg-black transition-all"
            >
              <legend className="legend">Command Palette</legend>
              <Combobox
                // @ts-ignore
                onChange={(chapter: MenuItemType) => {
                  setOpen(false);
                  navigate(`/${chapter.href}`);
                }}
              >
                <div className="relative -mt-1">
                  <FontAwesomeIcon
                    icon={faSearch}
                    className="pointer-events-none absolute top-3.5 left-4 h-5 w-5 text-green-500"
                    aria-hidden="true"
                  />
                  <Combobox.Input
                    className="placholder h-12 w-full border-0 bg-transparent pl-11 pr-4 text-gray-300 placeholder-gray-500 outline-none sm:text-sm"
                    placeholder="SEARCH..."
                    onChange={(event) => setQuery(event.target.value)}
                  />
                </div>

                {filteredChapters.length > 0 && (
                  <Combobox.Options
                    static
                    className="max-h-72 scroll-py-2 overflow-y-auto border-t border-green-600 py-2 text-sm text-gray-300"
                  >
                    {filteredChapters.map((chapter) => (
                      <Combobox.Option
                        key={chapter.href}
                        value={chapter}
                        className={({ active }) =>
                          classNames(
                            "cursor-default select-none px-4 py-2",
                            active && "bg-green-600 text-white"
                          )
                        }
                      >
                        {chapter.name}
                      </Combobox.Option>
                    ))}
                  </Combobox.Options>
                )}

                {query !== "" && filteredChapters.length === 0 && (
                  <p className="p-4 text-center text-sm text-gray-300">
                    No actions found.
                  </p>
                )}
              </Combobox>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition.Root>
  );
}
